import React, {useEffect, useState} from 'react'

import qr from '@vkontakte/vk-qr';

import './UserUrl.css'

const UserUrl = ({links}) => {
    const [linksGrid, setLinksGrid] = useState(null)
    useEffect(() => {
        console.log(links);
        let items = []
        links.items.map(x => {
            const qr_code = qr.createQR(x.short_url, {
                qrSize: 256,
                isShowLogo: true,
                isShowBackground: false
            });
            let data = new Date(x.timestamp)
            let date = `${data.getDate() < 10 ?
                '0' + data.getDate() :
                 data.getDate()}.${data.getMonth() < 10 ?
                    "0" + (data.getMonth() + 1) : 
                    data.getMonth() + 1 }.${data.getFullYear()} ${data.getHours() < 10 ?
                        "0" + data.getHours() :
                         data.getHours()}:${data.getMinutes() < 10 ?
                            "0" + data.getMinutes() :
                             data.getMinutes()}`
            items.push(
                <div key={x.timestamp} className='links-grid-items'>
                    <div className='links-qr-code' dangerouslySetInnerHTML={{
                        __html: qr_code
                    }}></div>
                    <div className='links-body'>
                        <div className='links-short-url'>{x.short_url}</div>
                        <div className='links-url'>{x.url}</div>
                        <div className='links-date'>{date}</div>
                    </div>
                </div>
            )
        })
        setLinksGrid(items)
    }, [links])

    return linksGrid
}

export default UserUrl